.line-burger {
  display: block;
  position: absolute;
  top: 50%;
  left: 0;
  width: 25px;
  height: 3px;
  background-color: #000000;
  transition: 0.5s width;
}
.line-burger:after, .line-burger:before {
  content: "";
  position: absolute;
  width: 25px;
  height: 3px;
  background-color: #000000;
  left: 0;
  transition: 0.5s;
}
.line-burger:after {
  top: calc(100% + 5px);
}
.line-burger:before {
  bottom: calc(100% + 5px);
}

.line-active {
  width: 0;
}
.line-active:after {
  top: 9px;
  transform: rotate(-45deg);
  transform-origin: 0 0;
}
.line-active:before {
  bottom: 9px;
  transform: rotate(45deg);
  transform-origin: 0 100%;
}

